<template>
  <div class="app-container">
    <el-card>
      <div class="main-title">
        <span>分销员昵称：{{ channelUsernickName }}</span>
        <span>分销员手机号：{{ channelUserPhone }}</span>
        <span>绑定用户数：{{ total }}</span>
        <span>所属分组：{{ channelGroupName }}</span>
      </div>
    </el-card>
    <!-- <el-card class="operate-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="积分来源：">
              <el-select v-model="value" placeholder="默认选择全部" clearable>
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </el-form-item>
          <el-form-item label="起止时间：">
            <el-date-picker v-model="list.time"
                            value-format="yyyy-MM-dd"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card> -->

    <!-- 数据列表 -->
    <!-- <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>

    </el-card> -->
    <el-card class="operate-container"
             shadow="never">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable">
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="用户昵称"
                         min-width="160"
                         align="center">
          <template slot-scope="scope">{{scope.row.channelUsernickName}}</template>
        </el-table-column>
        <el-table-column label="用户手机号"
                         width="160"
                         align="center">
          <template slot-scope="scope">{{scope.row.channelUserPhone == '' ? '--' : scope.row.channelUserPhone}}</template>
        </el-table-column>
        <el-table-column width="150"
                         align="center">
          <template slot="header"
                    slot-scope="scope">
            <el-tooltip class="item"
                        effect="dark"
                        content="累计交易笔数是指该用户支付过的订单笔数，剔除退款，不剔除部分退款"
                        placement="top">
              <el-button style="color:#909399;font-weight:600"
                         type="text">累计交易笔数<i class="el-icon-question"></i></el-button>
            </el-tooltip>
          </template>
          <!-- <template slot-scope="scope">{{scope.row.integralNum}}</template> -->
          <template slot-scope="scope">
            <span>
              {{scope.row.orderTotal}}
            </span>
          </template>
        </el-table-column>
        <el-table-column width="200"
                         align="center">
          <template slot="header"
                    slot-scope="scope">
            <el-tooltip class="item"
                        effect="dark"
                        content="累计交易金额是指该用户所有支付过的订单金额，不剔除退款；"
                        placement="top">
              <el-button style="color:#909399;font-weight:600"
                         type="text">累计交易金额<i class="el-icon-question"></i></el-button>
            </el-tooltip>
          </template>
          <template slot-scope="scope">
            {{scope.row.orderAmount}}
          </template>
        </el-table-column>
        <el-table-column label="被绑定时间"
                         width="200"
                         align="center">
          <template slot-scope="scope">
            {{scope.row.createTime}}
          </template>
        </el-table-column>
        <!-- <el-table-column label="状态"
                         width="200"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.isDeleteName }}
          </template>
        </el-table-column> -->
        <el-table-column label="操作"
                         width="200"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       @click="untying(scope.row)">解绑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-between"
           style="padding-top:20px;">
        <div>
          <el-button @click="goBack()"
                     type="primary">
            返回上一页
          </el-button>
        </div>
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 20, 30]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import {
  findChannelUserByParentId,
  removeBindByid
} from '@/api/distributionManagement/distributor'
export default {
  data() {
    return {
      list: {
        time: '',
      },
      id: this.$route.query.id,
      channelUsernickName: this.$route.query.channelUsernickName,
      channelGroupName: this.$route.query.channelGroupName,
      channelUserPhone: this.$route.query.channelUserPhone,
      data: {},
      tableList: [],
      pageNum: 1,
      page_size: 15,
      total: 0,
    }
  },
  created() {
    this.searchList()
  },
  mounted() {

  },
  methods: {
    //查询数据列表
    searchList() {
      findChannelUserByParentId({
        shopId: this.shop.shopId,
        userId: this.id,
        pageNum: this.pageNum,
        pageSize: this.page_size,
      }).then(res => {
        if (res.code == 200) {
          this.data = res.data
          this.tableList = res.data.list
          this.total = res.data.total
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          })
        }

      })
    },

    untying(row) {
      this.$confirm('是否解绑该用户？解绑后，该用户再购买将不会给该分销员以及上级分销员返佣。', '用户解绑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        removeBindByid({
          userId: row.cUserId,
          shopId: this.shop.shopId
        }).then(res => {
          if (res.data == true) {
            this.$notify({
              message: '解绑成功',
              type: 'success'
            });
            this.searchList()
          } else {
            this.$notify.error({
              title: '失败',
              message: res.message
            });
          }
        })
      }).catch((err) => {
        console.log(err)
      });
    },

    //返回
    goBack() {
      this.$store.dispatch('delView', this.$route)
      this.$router.push({
        path: '/distributionManagement/distributor',
      })
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 20px 20px;
  .main-title {
    display: flex;
    justify-content: space-around;
    color: #f59a23;
    // span{
    //   flex:1
    // }
  }
}
.btn-add {
  float: left;
  margin-left: 10px;
}
</style>